<!-- by 添少 -->
<template>
  <section class="fixed-layout">
    <section class="fixed-layout-header">
      <slot name="header"></slot>
    </section>
    <section class="fixed-layout-container">
      <slot></slot>
    </section>
    <section class="fixed-layout-footer">
      <slot name="footer"></slot>
    </section>
  </section>
</template>

<script>
  export default {
    name: 'fixed-layout'
  }
</script>

<style lang="scss" scoped>
  .fixed-layout {
    height: 100vh;
    display: flex;
    flex-direction: column;
  }

  .fixed-layout-header {
    flex: 0 0 auto;
  }

  .fixed-layout-container {
    overflow: hidden;
    overflow-y: scroll;
    overflow-scrolling: touch;
    flex: 1;
  }

  .fixed-layout-footer {
    flex: 0 0 auto;
  }
</style>



